.dynamic-filter {
    width: 100%;
    padding: .25rem;
    min-width: 200px;
    max-width: 400px;
    max-height: 100%;
    overflow-y: auto;
}

.dynamic-filter-expandable[open] {
    padding-bottom: .75rem;
}

.dynamic-filter-expandable summary {
    font-weight: 600;
    user-select: none;

    padding-bottom: .25rem;
    margin-bottom: .25rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.dynamic-filter-expandable summary .clear {
    float: right;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 80%;
    color: dimgrey;
    cursor: pointer;
    width: 24px;
    height: 24px;
    background: none;
    border-radius: 50%;
    border-width: 0;
}

.dynamic-filter-expandable summary .clear:hover,
.dynamic-filter-expandable summary .clear:active {
    color: black;
}

.dynamic-filter-expandable summary .clear:active {
    background: lightgrey;
}

.dynamic-filter-expandable summary .clear:focus {
    box-shadow: 0 0 0 .2rem rgba(0, 0, 0, .1);
}

.dynamic-filter input:invalid {
    border-color: red;
}

.dynamic-filter-body {
    padding: .25rem;
}

.button-bar {
    display: flex;
    gap: .25rem;
    padding-bottom: .5rem;
    margin-bottom: .25rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}
