.list-filter .item {
    display: flex;
}

.list-filter .item input {
    margin-right: .25rem;
}

.list-filter .item label {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 85%;
    font-weight: 500;
}

.expander {
    display: flex;
    font-size: 85%;
    font-weight: 500;
}
