.range-filter {
    display: flex;
    flex-direction: column;
    gap: .25rem;
}

.range-filter-inputs {
    display: flex;
    width: 100%;
}

.range-filter-inputs .separator {
    padding: 0 .5rem;
    opacity: 50%;
    user-select: none;
    text-align: center;
}

.range-filter-inputs input {
    min-width: 0; /* inputs have intrinsic width */
    flex-grow: 1;
    flex-basis: 0;
    padding: .15em .5em;
    border-radius: 4px;
    border: 1px solid lightgrey;
    background: white;
    color: black;
}

.range-filter-slider {
    padding-top: .5rem;
    /* create new stacking context, otherwise the slider is shown over dropdowns */
    isolation: isolate;
}

.range-filter-legend {
    display: flex;
    font-size: 85%;
}

.range-filter-legend label {
    padding: .25rem;
}

.grow {
    flex-grow: 1;
}

:global(.range-slider).range-slider {
    height: 4px;
}

:global(.range-slider).range-slider :global(.range-slider__thumb) {
    width: 18px;
    height: 18px;
}
